.link {
  &-dark {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-primary-hover-underline {
    position: relative;
    display: inline-block;

    &:after {
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 100%;
      height: 2px;
      background: $primary;
      transform: scaleX(0);
      transform-origin: right center;
      transition: transform 0.5s cubic-bezier(0.694, 0, 0.335, 1);
      content: "";
    }

    &:hover {
      &:after {
        transform: scaleX(1);
        transform-origin: left center;
      }
    }
  }
}
