.section {
  @include padding(rem(80px 0));

  &-welcome {
    position: relative;
    overflow: hidden;

    &__bg {
      @include absolute-cover;
      @include bg-cover;

      z-index: 1;
      pointer-events: none;
    }

    &__content {
      @include padding(rem(70px 0 0));

      position: relative;
      z-index: 5;
    }

    &__content-bg {
      @include bg-contain;

      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 1105px;
      height: 906px;
      transform: translate(45%, 50%);
      pointer-events: none;

      @include media-breakpoint-up(xs) {
        transform: translate(45%, 30%);
      }
    }

    &__content-inner {
      position: relative;
      z-index: 5;
    }

    &__img {
      @include responsive-ratio(667, 514);

      position: relative;

      @include media-breakpoint-up(lg) {
        width: calc(100% + 54px);
      }

      img {
        @include absolute-cover;

        object-fit: contain;
        object-position: bottom center;
      }
    }
  }
}

.to-bottom-section-shift {
  @include margin-bottom(rem(-80px));
}
