.btn {
  @include inline-flex-center;

  // sizes
  &:not(.btn-sm, .btn-lg) {
    min-height: 3em;

    @include media-breakpoint-down(md) {
      font-size: rem(16px);
    }
  }

  &-sm {
    min-height: 2.5em;
  }

  // colors
  &-primary {
    @include button-variant($primary, $primary, $black, $teal-dark, $teal-dark, $black, darken($teal-dark, 8%), darken($teal-dark, 8%), $black);
  }
}
