.footer {
  @include padding(rem(60px 0 40px));

  background-color: $white-faded;

  &__logo {
    @include margin-bottom(rem(40px));

    display: flex;
    width: 100%;
    max-width: rem(180px);
    margin-right: auto;
    margin-left: auto;

    @include media-breakpoint-up(lg) {
      max-width: rem(214px);
    }
  }

  &__top-content {
    padding-bottom: rem(60px);

    @include media-breakpoint-up(xl) {
      padding-bottom: rem(80px);
    }
  }
}
