.stars-rating {
  @include list-reset;

  position: relative;
  display: inline-flex;
  font-size: rem(18px);

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    width: calc((var(--rating) / 5) * 5em);
    overflow: hidden;
    color: $yellow;
    font-family: "icomoon";
    line-height: 1;
    content: "\e901\e901\e901\e901\e901";
  }

  &__item {
    display: inline-flex;
    color: $aqua-faded;
  }
}
