.table-responsive {
  @extend %custom-scrollbar;
}

.table {
  &-compare {
    margin-bottom: 0;
    vertical-align: middle;

    @include media-breakpoint-up(md) {
      table-layout: fixed;
    }

    thead {
      vertical-align: inherit;

      th {
        position: relative;
        padding: rem(32px 16px 20px);

        &:nth-child(2) {
          background-color: $white;
          border-radius: $border-radius $border-radius 0 0;
        }

        .circle-with-icon {
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    tbody {
      td {
        padding: rem(16px);

        &:nth-child(2) {
          background-color: $white;
        }
      }

      tr {
        &:last-child {
          td {
            border-bottom-color: transparent;

            &:nth-child(2) {
              border-radius: 0 0 $border-radius $border-radius;
            }
          }
        }
      }
    }

    th,
    td {
      border-bottom-color: rgba($aqua, 0.4);

      @include media-breakpoint-down(md) {
        min-width: rem(200px);
      }
    }

    tr {
      th,
      td {
        &:first-child {
          padding-left: 0;
        }
      }
    }
  }
}
