.doc-with-list-block {
  padding: rem(32px 20px 0);
  background-color: $aqua-light;
  border-radius: $border-radius;
  box-shadow: $box-shadow-gray;

  @include media-breakpoint-up(sm) {
    padding: rem(0 40px 0 32px);
  }

  &__content {
    width: 100%;

    @include media-breakpoint-up(sm) {
      padding: rem(24px 0);
    }
  }

  &__img-wrap {
    text-align: center;

    @include media-breakpoint-up(sm) {
      padding-top: rem(24px);
    }
  }

  &__img {
    width: 100%;
    border-top-left-radius: rem(16px);
    border-top-right-radius: rem(16px);
  }
}
