.radial-gradient-bg {
  @include absolute-cover;

  z-index: 1;
  display: flex;
  pointer-events: none;

  &.reverse {
    .radial-gradient-bg {
      &__img {
        &.blue {
          right: 0;
          left: auto;
          transform: translate(30%, -50%);
        }

        &.green {
          right: auto;
          left: 0;
          transform: translate(-30%, 50%);
        }
      }
    }
  }

  &__content {
    position: relative;
    height: 100%;
  }

  &__img {
    position: absolute;
    z-index: 5;
    width: 870px;
    opacity: 0.5;

    &.blue {
      top: 0;
      left: 0;
      transform: translate(-30%, -50%);
    }

    &.green {
      right: 0;
      bottom: 0;
      transform: translate(30%, 50%);
    }
  }
}
