.list {
  &-with-border {
    @include list-reset;

    &__item {
      padding: rem(20px 0);
      border-bottom: 1px solid rgba($black, 0.1);
    }

    &__content {
      padding-left: var(--title-with-icon-indent);
    }
  }

  &-with-check {
    @include list-reset;

    &.h6 {
      li {
        padding-left: rem(24px);
      }
    }

    li {
      position: relative;
      padding-left: rem(22px);

      &:not(:last-child) {
        margin-bottom: rem(24px);
      }

      &:before {
        position: absolute;
        top: 0;
        left: 0;
        width: rem(22px);
        height: 1lh;
        min-height: rem(16px);
        background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'><path fill-rule='evenodd' clip-rule='evenodd' d='M14.6664 2.25339C14.7617 2.34393 14.8399 2.4544 14.8967 2.57848C14.9535 2.70257 14.9876 2.83784 14.9972 2.97657C15.0068 3.11529 14.9916 3.25475 14.9526 3.38697C14.9135 3.51919 14.8513 3.64158 14.7695 3.74714L7.12708 13.6302C7.04139 13.741 6.93597 13.831 6.81742 13.8945C6.69888 13.958 6.56977 13.9937 6.43821 13.9992C6.30664 14.0048 6.17546 13.9802 6.05289 13.9269C5.93032 13.8736 5.81902 13.7928 5.72596 13.6895L1.26786 8.74799C1.09392 8.54818 0.997746 8.28065 1.00004 8.00301C1.00233 7.72537 1.10292 7.45984 1.28012 7.2636C1.45733 7.06736 1.69698 6.95612 1.94746 6.95384C2.19794 6.95156 2.43921 7.05842 2.6193 7.2514L6.34882 11.3839L13.32 2.36916C13.4849 2.15623 13.7194 2.02458 13.9718 2.00314C14.2243 1.98169 14.4741 2.0708 14.6664 2.25339Z' fill='#{$success}'/></svg>"));
        background-repeat: no-repeat;
        background-position: left center;
        background-size: rem(16px);
        content: "";
      }
    }
  }
}
