.badge {
  @include inline-flex-center;

  gap: rem(6px);
  min-height: rem(38px);
  white-space: normal;

  .icomoon-check {
    font-size: rem(16px);
  }

  &-glass {
    @include inline-flex-center;

    position: relative;
    z-index: 0;
    overflow: hidden;
    text-align: center;
    background-color: rgba($white, 0.1);
    border-radius: $border-radius-pill;

    &:not(.sm, .lg) {
      gap: 0.5em;
      min-height: 3.16666666em;
      padding: 0.66666666em 1.33333333em;
      font-weight: $font-weight-medium;
      font-size: rem(10px);
      line-height: $line-height-xxs;

      @include media-breakpoint-up(xs) {
        font-size: $font-size-xxs;
      }

      > [class^="icomoon-"] {
        font-size: 1.33333333em;
      }
    }

    &.lg {
      gap: 0.33333333em;
      min-height: 2.66666666em;
      padding: 0.44444444em 0.88888888em;
      font-size: rem(16px);
      font-family: $font-family-sans-serif-2;
      line-height: $line-height-base;

      @include media-breakpoint-up(sm) {
        font-size: rem(18px);
      }

      > [class^="icomoon-"] {
        font-size: 0.88888888em;
      }
    }

    &:before {
      position: absolute;
      z-index: -2;
      padding: 1.5px;
      background: linear-gradient(135deg, rgba($white, 0.8) 0%, rgba($white, 0.1) 50%), linear-gradient(98.54deg, rgba($white, 0) 72.28%, rgba($white, 0.4) 98.26%);
      border-radius: inherit;
      content: "";
      pointer-events: none;
      inset: 0;
      mask:
        linear-gradient($white 0 0) content-box,
        linear-gradient($white 0 0);
      mask:
        linear-gradient($white 0 0) content-box,
        linear-gradient($white 0 0);
      mask-composite: exclude;
    }

    &:after {
      position: absolute;
      z-index: -1;
      border-radius: inherit;
      box-shadow: inset 5px 4px 6px rgba($black, 0.05);
      backdrop-filter: blur(35px);
      content: "";
      pointer-events: none;
      inset: 1px;
    }

    &__stars {
      font-size: 1.5em !important;
    }
  }

  &.bg-aqua-transparent {
    background-color: rgba($aqua, 0.1);
    border-color: $aqua;
    box-shadow: inset 5px 4px 6px rgba($black, 0.05);
  }
}
