.accordion {
  &-button {
    position: relative;
    padding-right: rem(102px);
    font-weight: inherit;
    font-size: inherit;
    background-color: transparent;
    border-radius: $accordion-border-radius;

    @include media-breakpoint-down(sm) {
      padding: rem(16px 64px 16px 16px);
    }

    &:not(.collapsed) {
      background-color: transparent;

      .accordion-button-icon {
        transform: $accordion-icon-transform;
      }

      &:before {
        opacity: 0;
      }
    }

    &:before {
      @include absolute-cover;

      background: $accordion-bg;
      border-radius: inherit;
      transition: opacity 0.3s ease-in-out;
      content: "";
      pointer-events: none;
    }

    &:after {
      content: none;
    }

    &:hover {
      color: $blue;
    }
  }

  &-button-icon {
    position: absolute;
    top: rem(16px);
    right: rem(16px);
    z-index: 5;
    width: $accordion-icon-width;
    height: 1lh;
    transition: $accordion-icon-transition;

    @include media-breakpoint-up(sm) {
      top: $accordion-padding-y;
      right: $accordion-padding-x;
    }

    &:before {
      @include size($accordion-icon-width);
      @include absolute-center;

      background-color: $white;
      background-image: escape-svg($accordion-button-icon);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 0.875rem;
      border-radius: $border-radius-pill;
      content: "";
    }
  }

  &-body {
    padding-top: 0;

    @include media-breakpoint-down(sm) {
      padding: rem(0 16px 16px);
    }

    @include media-breakpoint-up(sm) {
      padding-right: rem(102px);
    }
  }

  &-item {
    border-radius: $accordion-border-radius;
    transition: $accordion-transition;

    &.active {
      border-color: $aqua !important;
    }

    &:not(:first-of-type) {
      border-top: $accordion-border-width solid $accordion-border-color;
    }

    &:not(:last-child) {
      margin-bottom: rem(16px);

      @include media-breakpoint-up(lg) {
        margin-bottom: rem(24px);
      }
    }
  }
}
