.image-with-bg-rounds {
  position: relative;

  @include media-breakpoint-up(lg) {
    float: right;
    width: calc(100% + 80px);
  }

  &:before,
  &:after {
    position: absolute;
    display: block;
    border-radius: $border-radius-pill;
    content: "";
    aspect-ratio: 1 / 1;
  }

  &:before {
    top: 10%;
    left: -20%;
    width: 95%;
    background-color: $aqua-light;
  }

  &:after {
    top: 35%;
    right: -15%;
    width: 78%;
    background-color: $green-light;
  }

  &__img {
    @include responsive-ratio(574, 491);

    position: relative;
    z-index: 5;
  }
}
