.circle-with-icon {
  @include size(rem(32px));

  position: relative;
  display: inline-flex;
  border-radius: $border-radius-pill;

  @include media-breakpoint-up(lg) {
    @include size(rem(40px));
  }

  &.lg-small {
    @include size(rem(42px));

    @include media-breakpoint-up(lg) {
      @include size(rem(50px));
    }
  }

  &.lg {
    @include size(rem(48px));

    @include media-breakpoint-up(lg) {
      @include size(rem(60px));
    }
  }
}
