.header {
  display: flex;
  align-items: center;
  min-height: var(--header-height);
  padding: rem(8px 0);
  background-color: $light;
  box-shadow: 0 4px 20px 0 rgba($aqua, 0.2);
  transition: background-color 0.3s;

  &.active {
    background-color: $white;
  }

  &__logo {
    display: flex;
    width: 100%;
    max-width: rem(156px);

    @include media-breakpoint-up(lg) {
      max-width: rem(192px);
    }
  }

  &__logo-img {
    object-fit: contain;
    object-position: left center;
  }
}
