  // styles for h4
}

h5,
.h5 {
  // styles for h5
}

h6,
.h6 {
  // styles for h6
}

.bordered-list {
  > [class^="col-"] {
    &:not(:first-child) {
      @include media-breakpoint-up(xs) {
        position: relative;
      }

      &:before {
        position: absolute;
        top: 50%;
        left: 0;
        color: $dark;
        transform: translate(-50%, -50%);

        @include media-breakpoint-up(xs) {
          content: "|";
        }
      }
    }
  }
}

.text-with-decoration-line {
  display: inline-block;
  padding-bottom: 10px;
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100% 10px;

  &.text-aqua {
    background-image: escape-svg($text-with-decoration-line-aqua-img);
  }
}

.text-with-icon {
  position: relative;
  padding-left: rem(28px);

  &__icon {
    @include size(1em);
    @include inline-flex-center;

    position: absolute;
    top: 50%;
    left: 0;
    color: $black;
    font-size: rem(24px);
    background-color: $primary;
    border-radius: $border-radius-pill;
    transform: translateY(-50%);
  }
}

.text-attention {
  position: relative;
  padding-left: rem(32px);
  font-weight: $font-weight-medium;
  font-size: $font-size-xxs;
  line-height: 1.4;

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    display: inline-flex;
    color: $blue;
    font-size: rem(24px);
    font-family: "icomoon";
    line-height: 1;
    content: "\e909";
  }
}

.blockquote {
  position: relative;
  padding: rem(22px 0 0 22px);

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    color: $blue;
    font-size: $font-size-xs;
    font-family: "icomoon";
    font-style: normal;
    content: "\e912";
  }

  &-footer {
    &:before {
      content: "- ";
    }
  }
}

:root {
  --title-with-icon-indent: 2.5rem;

  @include media-breakpoint-up(lg) {
    --title-with-icon-indent: 3.875rem;
  }
}

.title-with-icon {
  position: relative;
  padding-left: var(--title-with-icon-indent);

  &__icon-holder {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-flex;
    width: rem(40px);
    height: 1lh;
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 5;
    display: inline-flex;
    font-size: rem(32px);
    transform: translateY(-50%);

    @include media-breakpoint-up(lg) {
      font-size: rem(40px);
    }
  }
}
