.card {
  &-step {
    display: flex;
    width: 100%;
    padding-top: rem(24px);

    @include media-breakpoint-up(sm) {
      padding-top: rem(30px);
    }

    &__content {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: rem(40px 16px 16px);
      text-align: center;
      background-color: $white;
      border-radius: $border-radius;
      box-shadow: $box-shadow-gray;

      @include media-breakpoint-up(sm) {
        padding: rem(52px 32px 32px);
      }
    }

    &__top-content {
      flex-grow: 1;
      padding-bottom: rem(24px);
    }

    &__number-icon {
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 5;
      display: inline-flex;
      transform: translate(-50%, -50%);
    }

    &__img {
      @include responsive-ratio(300, 170);

      position: relative;
      max-width: 300px;
      margin: 0 auto;
    }
  }

  &-therapist {
    width: 100%;
    padding: rem(24px 16px);
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow-gray;

    @include media-breakpoint-up(xs) {
      padding: rem(32px);
    }

    &__img {
      @include responsive-ratio(292, 240);

      position: relative;
      overflow: hidden;
      background-color: $gray-100;
      border-radius: $border-radius-sm;
    }

    &__main-content {
      padding-top: rem(20px);
      text-align: center;

      + .card-therapist__text-content {
        margin-top: rem(20px);
        padding-top: rem(20px);
        border-top: 1px solid $gray-300;
      }
    }
  }

  &-bonus {
    width: 100%;
    overflow: hidden;
    border-radius: $border-radius;
    box-shadow: $box-shadow-gray;

    @include media-breakpoint-up(xxl) {
      width: rem(528px);
    }

    &.blue {
      background-color: $light;

      .card-bonus__header {
        color: $white;
        background-color: $blue-dark;
      }

      .card-bonus__title,
      .card-bonus__list-title {
        color: $blue-dark;
      }
    }

    &.yellow {
      background-color: $yellow-faded;

      .card-bonus__header {
        background-color: $yellow-dark;
      }
    }

    &__header {
      padding: rem(24px 16px);

      @include media-breakpoint-up(sm) {
        padding: rem(32px);
      }
    }

    &__header-arrow-icon {
      font-size: rem(24px);

      @include media-breakpoint-up(sm) {
        font-size: rem(32px);
      }
    }

    &__logo {
      max-width: rem(120px);

      @include media-breakpoint-up(sm) {
        max-width: rem(170px);
      }
    }

    &__title-block {
      padding: rem(24px 16px);
      text-align: center;
      background-color: $white;

      @include media-breakpoint-up(sm) {
        padding: rem(24px 32px);
      }
    }

    &__body {
      padding: rem(24px 16px);

      @include media-breakpoint-up(sm) {
        padding: rem(32px);
      }
    }

    &__img {
      @include responsive-ratio(344, 282);

      position: relative;
      max-width: rem(344px);
      margin: 0 auto rem(24px);
    }
  }

  &-testimonial {
    padding: rem(24px 16px);
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow-gray;

    @include media-breakpoint-up(sm) {
      padding: rem(32px);
    }

    &__img {
      flex-shrink: 0;
      width: rem(64px);
      object-fit: cover;
      border-radius: $border-radius-pill;
      aspect-ratio: 1 / 1;
    }
  }
}
